* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.main_div {
  width: 100%;
  height: 100vh;
  background-color: #7bed9f;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  text-align: center;
  align-content: center;
}

.center_div {
  width: 30%;
  height: 80vh;
  background-color: #f4f4f4;
  border-radius: 6px;
  box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
}

h1 {
  color: rgba(0, 0, 0, 0.87);
  font-size: 50px;
  font-weight: 500;
  text-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
  margin-bottom: 30px;
  font-family: 'Bebas Neue', cursive;
  letter-spacing: 0.02857em;
  text-transform: capitalize;
  min-width: 74px;
}

input {
  border: none;
  outline: none;
  width: 40%;
  height: 30px;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  background-color: transparent;
  border-bottom: 2px solid #7bed;
  margin-right: 20px;
}

.center_div .new_Btn {
  border-radius: 50%;
  width: 50px;
  height: 55px;
  color: white;
  background-color: #16a085;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.5);
}

.center_div .new_Btn:hover {
  background-color: #8566aa;
  color: white;
}

ul {
  margin-top: 30px;
}

ul li {
  padding-left: 0px;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  color: #8566aa;
  text-transform: capitalize;

}

.todo_style {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}

.todo_style .deleteIcon {

  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: #8566aa;
  color: aliceblue;
  margin: 0 25px 0 35px;
}

.todo_style .deleteIcon:hover {
  background-color: transparent;
  color: crimson;
  box-shadow: 5px 5px 15px -5px rgba(255, 0, 0, 0.5);
}

.MuiSvgIcon-root {
  width: 2rem;
  height: 2rem;
}

@media (max-width:768px) {
  .center_div {
    width: 70%;
  }
}